                    <small class="font-bold">Tanggal Pesan</small>
                </div>
                <div class="col-span-2">
                    <datepicker disabled :placeholder="'-- Tanggal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="formPesanan.tanggal_pesan" ></datepicker>
                </div>
            </div>
            <div class="grid grid-cols-3 gap-4 mb-4">
                <div class="col-span-1">
                    <small class="font-bold">Tanggal Ambil</small>
                </div>
                <div class="col-span-2">
                    <datepicker disabled :placeholder="'-- Tanggal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="formPesanan.tanggal_ambil" ></datepicker>
                </div>
            </div>
            <div class="grid grid-cols-3 gap-4 mb-4">
                <div class="col-span-1">
                    <small class="font-bold">Jenis</small>
                </div>
                <div class="col-span-2">
                    <ul class="leftx">
                        <li v-for="(item, index) in tipe" :key="index">
                            <vs-radio disabled v-model="formPesanan.tipe" :vs-value="item"><small>{{ item }}</small></vs-radio>
                        </li>
                    </ul>

                </div>
            </div>
            <div class="grid grid-cols-3 gap-4 mb-8">
                <div class="col-span-1">
                    <small class="font-bold">Dp</small>
                </div>
                <div class="col-span-2">
                    <input type="number" disabled class="vs-inputx vs-input--input normal"  v-model="formPesanan.dp"  style="border: 1px solid rgba(0, 0, 0, 0.2); text-align:right; direction: rtl;" >
                    <vs-checkbox :disabled="formPesanan.dp_lunas == true" @change="updateDpPesanan()" v-model="formPesanan.dp_lunas" style="margin-top: -30px; margin-left: 10px;">{{ formPesanan.dp_lunas == true ? 'Lunas' : 'Belum Lunas' }}</vs-checkbox>
                </div>
            </div>

            <div class="grid grid-cols-3 gap-4">
                <div class="col-span-1">
                    <small class="font-bold">Catatan</small>
                </div>
                <div class="col-span-2">
                    <vs-textarea disabled class="w-full" v-model="formPesanan.catatan" />
                    <vs-button  @click="editCatatanPesanan()" class="float-right" color="dark" type="line" icon-pack="feather" icon="icon-edit" style="margin-top: -55px; margin-right: 5px; border-bottom-width: 0px;"> </vs-button>
                </div>
            </div>
            <vs-collapse class="p-0">
                <vs-collapse-item>
                    <div slot="header">Rincian Pesanan</div>
                    <vs-table :data="formPesanan.rincian_pesanan">
                        <template slot="thead" >
                            <vs-th sort-key="menu" class="bg-primary text-white">
                                <div class="custom-th">Menu</div>
                            </vs-th>
                            <vs-th sort-key="qty" class="bg-primary text-white">
                                <div class="custom-th">Qty</div>
                            </vs-th>
                            <vs-th sort-key="total" class="bg-primary text-white">
                                <div class="custom-th">Total</div>
                            </vs-th>
                        </template>

                        <template slot-scope="{data}">
                            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" >
                            <vs-td :data="tr.menu">
                                {{tr.menu}}
                            </vs-td>
                            <vs-td width="15px" :data="tr.qty">
                                {{tr.qty}}
                            </vs-td>
                            <vs-td class="text-right" width="100px" :data="tr.total" >
                                {{currency(tr.total)}}
                            </vs-td>
                        </vs-tr>
                        </template>
                    </vs-table>
                    <div class="w-full text-right mb-2">
                        <span class="font-bold">Total Rp. {{ currency(totalRincianPesanan) }}</span>
                    </div>
                </vs-collapse-item>
                <vs-collapse-item>
                    <div slot="header">Rincian Pembelian</div>
                    <div class="flex mb-2">
                        <div class="w-4/5 pt-2">
                            <small class="font-bold">Rincian Pembelian</small>
                        </div>
                        <div class="w-1/5">
                            <vs-button size="small" class="w-full p-2" @click="editRincianPembelian" color="success" type="filled">Tambah</vs-button>
                        </div>
                    </div>
                    <vs-table :data="formPesanan.update_rincian_pembelian">
                        <template slot="thead" >
                            <vs-th sort-key="tanggal" class="bg-primary text-white">
                                <div class="custom-th">Tanggal</div>
                            </vs-th>
                            <vs-th sort-key="gerai" class="bg-primary text-white">
                                <div class="custom-th">Gerai</div>
                            </vs-th>
                            <vs-th sort-key="total" class="bg-primary text-white">
                                <div class="custom-th">Total</div>
                            </vs-th>
                            <vs-th class="bg-primary text-white">
                                <div class="custom-th">Aksi</div>
                            </vs-th>
                        </template>

                        <template slot-scope="{data}">
                            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" >
                                <vs-td width="120px" :data="tr.tanggal">
                                    {{tr.tanggal}}
                                </vs-td>
                                <vs-td :data="tr.gerai">
                                    {{tr.gerai}}
                                </vs-td>
                                <vs-td class="text-right" width="100px" :data="tr.subtotal" >
                                    {{currency(tr.subtotal)}}
                                </vs-td>
                                <vs-td width="20px">
                                    <vx-tooltip text="Hapus" style="display:inline">
                                        <vs-button @click="removeupdateRincian(tr)" color="danger" type="filled" size="small" icon="delete" style="display:inline"></vs-button>
                                    </vx-tooltip>
                                </vs-td>
                            </vs-tr>
                        </template>
                    </vs-table>
                    <div class="w-full text-right mb-2">
                        <span class="font-bold">Total Rp. {{ currency(totalupdatePesananRincianPembelian) }}</span>
                    </div>
                </vs-collapse-item>
            </vs-collapse>
            <div class="flex">
                <div class="w-1/2 text-left">
                    <vs-button class="mb-2 mt-5" color="rgb(0,207,232)" @click="copyNotaPesanan()" icon="download">Nota Pesanan</vs-button>
                </div>
                <div class="w-1/2 text-right">
                    <vs-button class="mb-2 mt-5" :disabled="!validateupdatePesanan" color="success" @click="updatePesanan()">Simpan</vs-button>
                </div>
            </div>
          </vs-popup>
          <div v-show="showNota == true" class ="struk mx-auto" ref="printcontent" style="width: 219.21px;">
              <vx-card class="watermarked-card" style="width: 219.21px;">
                  <img v-show="formPesanan.dp_lunas == true" src="@/assets/images/pages/sudah_lunas.png" alt="Lunas" class="watermark-image">
                  <img v-show="formPesanan.dp_lunas == false" src="@/assets/images/pages/belum_lunas.png" alt="Belum Lunas" class="watermark-image">
                  <img class="mx-auto mb-3" src="@/assets/images/logo/logo_hisana.png" width="120px" alt="img-logo">
                  <div class="w-full content text-center">-----------------------------</div>
                  <div class="w-full content text-center">NOTA PESANAN</div>
                  <div class="w-full content text-center">-----------------------------</div>
                  <div class="content"  v-for="(tr, index) in formPesanan.rincian_pesanan" :key="index">
                      <div class="w-full">{{ tr.menu }}</div>
                      <div class="flex">
                          <div class="w-1/2">
                              {{ tr.qty }}xRp{{ currencyNota(tr.harga) }}
                          </div>
                          <div class="w-1/2 text-right">
                              Rp{{ currencyNota(tr.total) }}
                          </div>
                      </div>
                  </div>
                  <div class="w-full content text-right">-----------------</div>
                  <div class="flex content">
                      <div class="w-1/2 text-right">TOTAL</div>
                      <div class="w-1/2 text-right">Rp{{ currencyNota(totalRincianPesanan) }}</div>
                  </div>
                  <div class="w-full content text-center mb-3">-----------------------------</div>
                  <div class="flex content">
                      <div class="w-40">Pemesan</div>
                      <div class="w-4">:</div>
                      <div class="w-40">{{ convertToOriginalString(this.$route.params.name) }}</div>
                  </div>
                  <div class="flex content">
                      <div class="w-40">Tanggal Pesan</div>
                      <div class="w-4">:</div>
                      <div class="w-40">{{ formattedDateNota(formPesanan.tanggal_pesan) }}</div>
                  </div>
                  <div class="flex content">
                      <div class="w-40">Tanggal Ambil</div>
                      <div class="w-4">:</div>
                      <div class="w-40">{{ formattedDateNota(formPesanan.tanggal_ambil) }}</div>
                  </div>
                  <div class="flex content">
                      <div class="w-40">Dp</div>
                      <div class="w-4">:</div>
                      <div class="w-40">Rp{{ currencyNota(formPesanan.dp) }}</div>
                  </div>
                  <div class="flex content">
                      <div class="w-40">Catatan</div>
                      <div class="w-4">:</div>
                  </div>
                  <div class="w-full content">{{ formPesanan.catatan }}</div>
              </vx-card>
          </div>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import 'intro.js/introjs.css';
import introJs from 'intro.js';
import html2canvas from 'html2canvas'
// import { copyImageToClipboard, requestClipboardWritePermission } from 'copy-image-clipboard'

export default {
    data() {
        return{
            modaladdPesanan: false,
            modaleditPesanan: false,
            modaladdRincianPembelian: false,
            modaleditRincianPembelian: false,
            modalRincianPembelianItem: false,
            modalRincianPesanan: false,
            modaleditCatatanPesanan: false,
            autoItem: false,
            showNota: false,
            countPesananDashboard: '',
            actionRincianPembelianItem: '',
            pesanan : [],
            gerai: [],
            tipe: [],
            formPesanan: {
                kode_pesanan: '',
                no_struk: '',
                tanggal_pesan: '',
                tanggal_ambil: '',
                tipe: '',
                dp: '',
                catatan: '',
                edit_catatan: '',
                dp_lunas: false,
                rincian_pesanan: JSON.parse(localStorage.getItem('rincian_pesanan')) ? JSON.parse(localStorage.getItem('rincian_pesanan')) : [],
                rincian_pembelian: JSON.parse(localStorage.getItem('rincian_pembelian')) ? JSON.parse(localStorage.getItem('rincian_pembelian')) : [],
                update_rincian_pembelian: JSON.parse(localStorage.getItem('update_rincian_pembelian')) ? JSON.parse(localStorage.getItem('update_rincian_pembelian')) : [],
            },
            formRincianPembelian:{
                gerai: '',
                struk: '',
                tanggal: '',
                subtotal: '',
                rincian: JSON.parse(localStorage.getItem('Item')) ? JSON.parse(localStorage.getItem('Item')) : [],
                updateRincian: JSON.parse(localStorage.getItem('updateItem')) ? JSON.parse(localStorage.getItem('updateItem')) : []
            },
            formRincianPembelianItem: {
                menu: '',
                qty: '',
                harga: ''
            },
            formRincianPesanan: {
                menu: '',
                qty: '',
                harga: ''
            }
        }
    },

    components:{
        Datepicker,
        introJs,
        html2canvas
    },

    computed: {
        validatenoTransaksi() {
        return !this.errors.any()
            && this.formRincianPembelian.gerai != ''
            && this.formRincianPembelian.struk != ''
        },

        validateaddPesanan() {
        return !this.errors.any()
            && this.formPesanan.tanggal_pesan != ''
            && this.formPesanan.tanggal_ambil != ''
            && this.formPesanan.tipe != ''
            && this.formPesanan.dp != ''
        },

        validateupdatePesanan() {
        return !this.errors.any()
            && this.formPesanan.tanggal_pesan != ''
            && this.formPesanan.tanggal_ambil != ''
            && this.formPesanan.tipe != ''
            && this.formPesanan.dp != ''
            // && this.formPesanan != ''
        },

        validateaddRincianPembelian() {
        return !this.errors.any()
            && this.formRincianPembelian.gerai != ''
            && this.formRincianPembelian.struk != ''
            && this.formRincianPembelian.tanggal != ''
            && this.formRincianPembelian.rincian != ''
        },

        validateupdateRincianPembelian() {
        return !this.errors.any()
            && this.formRincianPembelian.gerai != ''
            && this.formRincianPembelian.struk != ''
            && this.formRincianPembelian.tanggal != ''
            && this.formRincianPembelian.updateRincian != ''
        },

        validateaddRincianPembelianItem() {
        return !this.errors.any()
            && this.formRincianPembelianItem.menu != ''
            && this.formRincianPembelianItem.qty != ''
            && this.formRincianPembelianItem.harga != ''
        },

        validateaddRincianPesanan() {
        return !this.errors.any()
            && this.formRincianPesanan.menu != ''
            && this.formRincianPesanan.qty != ''
            && this.formRincianPesanan.harga != ''
        },

        validateupdateDpPesanan() {
        return !this.errors.any()
            && this.formPesanan.dp != ''
        },

        validateupdateCatatanPesanan() {
        return !this.errors.any()
            && this.formPesanan.catatan != ''
        },

        totalRincianPesananItem() {
            return this.formRincianPesanan.qty * this.formRincianPesanan.harga
        },

        totalRincianPembelianItem() {
            return this.formRincianPembelianItem.qty * this.formRincianPembelianItem.harga
        },

        totaladdRincianPembelian() {
            return this.formRincianPembelian.rincian.reduce(function (items, data) {
                return items + data.total
            }, 0)
        },

        totalupdateRincianPembelian() {
            return this.formRincianPembelian.updateRincian.reduce(function (items, data) {
                return items + data.total
            }, 0)
        },

        totalRincianPembelian() {
            return this.formPesanan.rincian_pembelian.reduce(function (items, data) {
                return items + data.subtotal
            }, 0)
        },

        totalRincianPesanan() {
            return this.formPesanan.rincian_pesanan.reduce(function (items, data) {
                return items + data.total
            }, 0)
        },

        totalupdatePesananRincianPembelian() {
            return this.formPesanan.update_rincian_pembelian.reduce(function (items, data) {
                return items + data.subtotal
            }, 0)
        },

        totalPesananDashboard() {
            return this.pesanan.reduce(function (items, data) {
                return items + data.total_pesanan
            }, 0)
        },

    },

    mounted() {
        this.loadData();
    },

    methods:{
        onNumericInput() {
            // Remove any non-numeric characters from the input
            this.formPesanan.dp = this.formPesanan.dp.replace(/\D/g, '');
        },
        // Get Data
        loadData() {
            this.$vs.loading();
            this.$http.get('pos/pembelian/getData/' +  this.$route.params.id)
            .then(response => {
                const updatedResponse = response.data.message.map(obj => ({
                    ...obj,
                    total_pesanan: parseInt(obj.total_pesanan.replace(/,/g, ""), 10),
                    total_pembelian: parseInt(obj.total_pembelian.replace(/,/g, ""), 10),
                    dp: parseInt(obj.dp.replace(/,/g, ""), 10)
                }));
                this.pesanan = updatedResponse
                this.countPesananDashboard = this.pesanan.length
            })
            .catch(err => {
                this.$vs.loading.close();
                this.$swal.fire(
                    'Gagal !',
                    err.response.data.message,
                    'error'
                )
            })
            .finally(() => {this.$vs.loading.close()});
        },

        loadTipe() {
            this.$vs.loading();
            this.$http.get('pos/pesanan/tipe')
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.$vs.loading.close();
                    this.tipe = response.data.message
                }
            })
            .catch(err => {
                this.$vs.loading.close();
                this.$swal.fire(
                    'Gagal !',
                    err.response.data.message,
                    'error'
                )
            })
        },

        loadGerai() {
            this.$http.get('pos/gerai/getData')
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.gerai = response.data.message;
                }
            })
            .catch(error => {error})
        },

        loadNoStruk() {
            this.$http.get('pos/pesanan/nomor')
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.formPesanan.no_struk = response.data.message;
                }
            })
            .catch(error => {error})
        },

        searchNoTransaksi(action) {
            this.$vs.loading();
            this.$http.get('pos/gerai/getBillDetails/' + this.formRincianPembelian.gerai.kode + '/' + this.formRincianPembelian.struk)
            .then(response => {
                if(response.data.status == "SUKSES" && response.data.message != ''){
                    if(action == 'insert') {
                        this.formRincianPembelian.rincian = [];
                        response.data.message.forEach(item => {
                            const newItem = { ...item, total: parseInt(item.total) };
                            this.formRincianPembelian.rincian.push(newItem);
                        });
                        localStorage.setItem('Item', JSON.stringify(this.formRincianPembelian.rincian))
                        this.formRincianPembelian.tanggal = response.data.tanggal
                        this.autoItem = true
                    } else {
                        this.formRincianPembelian.updateRincian = [];
                        response.data.message.forEach(item => {
                            const newItem = { ...item, total: parseInt(item.total) };
                            this.formRincianPembelian.updateRincian.push(newItem);
                        });
                        localStorage.setItem('updateItem', JSON.stringify(this.formRincianPembelian.updateRincian))
                        this.formRincianPembelian.tanggal = response.data.tanggal
                        this.autoItem = true
                    }
                } else {
                    this.$swal.fire(
                        'Gagal !',
                        'No Transaksi Tidak Ditemukan !',
                        'error'
                    )
                }
            })
            .catch(error => {error})
            .finally(() => {this.$vs.loading.close()});
        },

        // Modal
        addPesanan() {
            this.modaladdPesanan = true;
            this.tipe = [];
            this.formPesanan.no_struk = '';
            this.formPesanan.tanggal_pesan = '';
            this.formPesanan.tanggal_ambil = '';
            this.formPesanan.tipe = '';
            this.formPesanan.dp = '';
            this.formPesanan.catatan = '';
            this.formPesanan.rincian_pesanan = [];
            this.loadNoStruk();
            this.loadTipe();
            let rincianPesanan =  JSON.parse(localStorage.getItem('rincian_pesanan')) ? JSON.parse(localStorage.getItem('rincian_pesanan')) : [];
            this.formPesanan.rincian_pesanan.push(...rincianPesanan);
        },

        editPesanan(kode) {
            this.modaleditPesanan = true;
            this.formPesanan.tipe = '';
            this.formPesanan.rincian_pesanan = [];
            this.formPesanan.update_rincian_pembelian = [];
            this.loadTipe();
            setTimeout(() => {
                let hasil = this.pesanan.filter(item => {
                    return item.kode == kode;
                });

                this.formPesanan.tanggal_pesan = hasil[0].tanggal_pesan;
                this.formPesanan.tanggal_ambil = hasil[0].tanggal_ambil;
                this.formPesanan.tipe = hasil[0].tipe;
                this.formPesanan.dp = hasil[0].dp;
                this.formPesanan.catatan = hasil[0].catatan;
                this.formPesanan.dp_lunas = hasil[0].dp_lunas;
                this.formPesanan.no_struk = hasil[0].no_struk;
                this.formPesanan.kode_pesanan = kode;
                let rincianPesanan = hasil[0].rincian_pesanan.map(obj => ({
                    ...obj,
                    total: parseInt(obj.total)
                }));
                this.formPesanan.rincian_pesanan.push(...rincianPesanan);

                let rincianPembelian = hasil[0].rincian_pembelian.map(obj => ({
                    ...obj,
                    subtotal: parseInt(obj.subtotal.replace(/,/g, ""), 10)
                }));
                this.formPesanan.update_rincian_pembelian.push(...rincianPembelian);
            }, 1000);

        },

        addRincianPesanan() {
            this.modalRincianPesanan = true;
            this.formRincianPesanan.menu = ''
            this.formRincianPesanan.qty = ''
            this.formRincianPesanan.harga = ''
        },

        addRincianPembelian() {
            this.modaladdRincianPembelian = true
            this.autoItem = false
            this.loadGerai();
            this.formRincianPembelian.gerai = '',
            this.formRincianPembelian.struk = '',
            this.formRincianPembelian.tanggal = '',
            this.formRincianPembelian.subtotal = '',
            this.formRincianPembelian.rincian = [],
            localStorage.removeItem('Item');
        },

        editRincianPembelian() {
            this.modaleditRincianPembelian = true
            this.autoItem = false
            this.loadGerai();
            this.formRincianPembelian.gerai = '',
            this.formRincianPembelian.struk = '',
            this.formRincianPembelian.tanggal = '',
            this.formRincianPembelian.subtotal = '',
            this.formRincianPembelian.updateRincian = [],
            localStorage.removeItem('updateItem');
        },

        addRincianPembelianItem(action) {
            this.modalRincianPembelianItem = true;
            this.formRincianPembelianItem.menu = '';
            this.formRincianPembelianItem.qty = '';
            this.formRincianPembelianItem.harga = '';
            this.actionRincianPembelianItem = action;
        },

        // Remove Data
        removeRincian(gerai) {
            this.$swal.fire({
                title: 'Apakah Anda Yakin ?',
                text: "Ingin Menghapus Data Rincian",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Ya'
            })
            .then((result) => {
                if (result.value) {
                    for (var item in this.formPesanan.rincian_pembelian) {
                        if (this.formPesanan.rincian_pembelian[item].gerai === gerai) {
                            this.formPesanan.rincian_pembelian.splice(item, 1)
                            break
                        }
                    }
                    localStorage.setItem('rincian_pembelian', JSON.stringify(this.formPesanan.rincian_pembelian))
                    this.$swal.fire({
                        title: 'Success!',
                        text:  'Data sudah dihapus',
                        icon: 'success',
                        timer: 3000,
                        showConfirmButton: false
                    });
                }
            });
        },

        removeupdateRincian(tr) {
            this.$swal.fire({
                title: 'Apakah Anda Yakin ?',
                text: "Ingin Menghapus Data Rincian",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Ya'
            })
            .then((result) => {
                if (result.value) {
                    if(tr.kode != '0') {
                        this.$http.delete('pos/pembelian/rincian/' + tr.kode)
                        .then(response=> {
                            if(response.data.status == "SUKSES"){
                                this.loadData();
                            }else {
                                this.$swal.fire(
