                        .catch(err=>{
                            this.$vs.loading.close();
                            this.$swal.fire(
                                'Gagal !',
                                err.response.data.message,
                                'error'
                            )
                        })
                        .finally(() => {this.$vs.loading.close()});
                    }

                    for (var item in this.formPesanan.update_rincian_pembelian) {
                        if (this.formPesanan.update_rincian_pembelian[item].gerai === tr.gerai) {
                            this.formPesanan.update_rincian_pembelian.splice(item, 1)
                            break
                        }
                    }
                    localStorage.setItem('update_rincian_pembelian', JSON.stringify(this.formPesanan.update_rincian_pembelian))

                    this.$swal.fire({
                        title: 'Success!',
                        text:  'Data sudah dihapus',
                        icon: 'success',
                        timer: 3000,
                        showConfirmButton: false
                    });
                }
