        async captureImage() {
            try {
                this.$swal.fire({
                  title: 'Success',
                  text: 'Nota Pesanan Berhasil di Download',
                  icon: 'success',
                  timer: 3000,
                  showConfirmButton: false
                });
                this.showNota = false;
                this.$vs.loading.close();

                const captureElement = this.$refs.printcontent;
                const canvas = await html2canvas(captureElement);
                const image = canvas.toDataURL('image/png');

                const link = document.createElement('a');
                link.href = image;
                link.download =  this.formPesanan.no_struk + '.png';
                link.click();

                const newTab = window.open();
                newTab.document.write(`
                  <style>
                      body {
                          display: flex;
                          justify-content: center;
                          align-items: center;
                          height: 100vh;
                          margin: 0;
                          background-color: black;
                      }
                      img {
                          max-width: 100%;
                          max-height: 100%;
                      }
                  </style>
                  <body>
                      <img src="${image}" alt="Captured Image">
                  </body>`);
                newTab.document.close();
            } catch (error) {
                this.showNota = false;
                this.$vs.loading.close();
                this.$swal.fire({
                  title: 'Oopss !',
                  text: error.message,
                  icon: 'error',
                  timer: 1500,
                  showConfirmButton: false
                });
            }
        },

        currencyNota(value) {
            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
        },

        currency(value) {
            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },

        formattedDateNota(value) {
            const date = new Date(value);
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, '0');
            const day = String(date.getDate()).padStart(2, '0');
            return `${day}-${month}-${year}`;
        },

        formattedDate(value) {
            const date = new Date(value);
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, '0');
            const day = String(date.getDate()).padStart(2, '0');
            return `${year}-${month}-${day}`;
        },

        convertToOriginalString(slug) {
            const words = slug.split("-");
            const originalString = words
                .map(word => {
                return word.charAt(0).toUpperCase() + word.slice(1);
                })
                .join(" ");
            return originalString;
        }
    }
